
import { parseTime, calculateNumber } from "@/utils/index";
import { GetAllBillsRequest, GetAnyDayBillsRequest } from "@/api/app";


const billMixin = {
  data() {
    return {
      startTime: "",
      endTime: "",
      show: false,
      currentDate: new Date(),
      minDate: new Date(2020, 0, 1),
      maxDate: new Date(2025, 11, 31),
      title: "请选择开始时间",
      type: "",
      showPopover: false,
      // 通过 actions 属性来定义菜单选项
      actions: [{ text: "按标记人" }, { text: "按操作人" }],
      popoverText: "按标记人",
      tableHeight: 700,
      tableData: [],
      activeNames: [],
      bills_in: [],
      bills_out: [],
      markList: [],
      operatorList: [],
      id: "",
      keyword: "",
      regex: /^(-?\d+(\.\d+)?)(\D+)?$/,

      dataType: 1,
      day: 0,
      statisticsObject: {
        usd_rate: 0, // 汇率
        rate: 0, // 费率
        totalIncomeRMB: 0, // 总金额人民币
        totalIncomeUSDT: 0, // 总金额美元
        PlanTotalRMB: 0, // 应下发人民币
        PlanTotalUSDT: 0, // 应下发美元
        issuedRMB: 0, // 已下发人民币
        issuedUSDT: 0, // 已下发美元
        unissuedRMB: 0, // 未下发人民币
        unissuedUSDT: 0, // 未下发人民币
      },
    };
  },
  created() {
    var currentDate = new Date();
    var hours = currentDate.getHours();
    var minutes = currentDate.getMinutes();
    var seconds = currentDate.getSeconds();
    console.log(hours, minutes, seconds)
    if(hours < 4 || (hours == 4 && minutes === 0 && seconds === 0)) {
      this.endTime = parseTime(new Date(), "{y}-{m}-{d}") + " 04:00";
      currentDate.setDate(currentDate.getDate() - 1);
      this.startTime = parseTime(currentDate, "{y}-{m}-{d}") + " 04:00";
    } else {
      this.startTime = parseTime(new Date(), "{y}-{m}-{d}") + " 04:00";
      currentDate.setDate(currentDate.getDate() + 1);
      this.endTime = parseTime(currentDate, "{y}-{m}-{d}") + " 04:00";
    }
    this.id = this.$route.query.group || "-4230097728";
    this.day = Number(this.$route.query.day || 0)
    this.getListshandle();
  },
  mounted() {},
  methods: {
    initHandle() {
      this.statisticsObject = {
        usd_rate: 0,
        rate: 0,
        totalIncomeRMB: 0,
        totalIncomeUSDT: 0,
        PlanTotalRMB: 0,
        PlanTotalUSDT: 0,
        issuedRMB: 0,
        issuedUSDT: 0,
        unissuedRMB: 0,
        unissuedUSDT: 0,
      };
    },
    getListshandle(search_bool) {
      this.day = 0
      GetAnyDayBillsRequest(this.id, this.day).then((res) => {
        this.getDataHandle(res, search_bool)
      });
    },
    getAnyDayListsHandle(search_bool) {
      GetAnyDayBillsRequest(this.id, this.day).then(res => {
         this.getDataHandle(res, search_bool)
      })
    },
    getDataHandle(res, search_bool) {
      var currentDate = new Date();
      var currentDay = parseTime(currentDate, "{m}-{d}");
      var currentYear = parseTime(currentDate, "{y}-{m}-{d}");
      this.bills_in = res.bills_in || [];
      this.bills_out = res.bills_out || [];
      this.bills_in.sort((a, b) => {
        let dateA = a.timestamp;
        let dateB = b.timestamp;
        return dateA - dateB;
      });
      this.bills_out.sort((a, b) => {
        let dateA = a.timestamp;
        let dateB = b.timestamp;
        return dateA - dateB;
      });

      this.bills_in = this.bills_in.map((d, index) => {
        let match = d.amount.match(this.regex);
        console.log(777, match, d)
        let rateAmount = d.after_rate_amount.match(this.regex);
        d.time = parseTime(d.timestamp, "{m}-{d} {h}:{i}:{s}");
        d.amount_num = Number(match[1]);
        d.amount_unit = match[3];
        d.after_rate_amount_num = Number(rateAmount[1]);
        d.after_rate_amount_unit = rateAmount[3];
        d.invalid = d.remark.indexOf('无效') > -1 ? true : false
        return d;
      });

      this.bills_out = this.bills_out.map((d) => {
        let match = d.amount.match(this.regex);
        let rateAmount = d.after_rate_amount.match(this.regex);
        d.time = parseTime(d.timestamp, "{m}-{d} {h}:{i}:{s}");
        d.amount_num = match[1];
        d.amount_unit = match[3];
        d.after_rate_amount_num = Number(rateAmount[1]);
        d.after_rate_amount_unit = rateAmount[3];
        d.invalid = d.remark.indexOf('无效') > -1 ? true : false
        return d;
      });
      if (this.keyword && search_bool) {
        this.bills_in = this.bills_in.filter((d) => {
          if (this.popoverText === "按标记人") {
            return d.other_name.indexOf(this.keyword) > -1;
          } else if (this.popoverText === "按操作人") {
            return d.name.indexOf(this.keyword) > -1;
          }
        });
        this.bills_out = this.bills_out.filter((d) => {
          if (this.popoverText === "按标记人") {
            return d.other_name.indexOf(this.keyword) > -1;
          } else if (this.popoverText === "按操作人") {
            return d.name.indexOf(this.keyword) > -1;
          }
        });
      }
      if(this.bills_out.length > 0) {
        this.setChartsData && this.setChartsData(this.bills_out, "chartOutRef");
      }
      if(this.bills_in.length > 0) {
        this.setChartsData && this.setChartsData(this.bills_in, "chartInRef");
      }
      
      this.getStatisticsData();
      this.formatBills(res, search_bool);
    },
    setDayHandle(type) {
      if(type === "previous") {
        this.day = this.day - 1
      } else if(type === "next") {
        if(this.day < 0) {
          this.day = this.day + 1
        } else {
          return
        }
      }
      this.keyword = '';
      var currentDate = new Date();
      var hours = currentDate.getHours();
      var minutes = currentDate.getMinutes();
      var seconds = currentDate.getSeconds();
      if(hours < 4 || (hours == 4 && minutes === 0 && seconds === 0)) {
        currentDate.setDate(currentDate.getDate() + this.day);
        this.endTime = parseTime(currentDate, "{y}-{m}-{d}") + " 04:00";
        currentDate.setDate(currentDate.getDate() - 1);
        this.startTime = parseTime(currentDate, "{y}-{m}-{d}") + " 04:00";

      } else {
        currentDate.setDate(currentDate.getDate() + this.day);
        this.startTime = parseTime(currentDate, "{y}-{m}-{d}") + " 04:00";
        currentDate.setDate(currentDate.getDate() + 1);
        this.endTime = parseTime(currentDate, "{y}-{m}-{d}") + " 04:00";

      }
      

      this.getAnyDayListsHandle()
    },
    getStatisticsData() {
      this.initHandle();
      if (this.bills_in.length > 0) {
        this.statisticsObject.usd_rate =
          this.bills_in[this.bills_in.length - 1].usd_rate;
        this.statisticsObject.rate =
          this.bills_in[this.bills_in.length - 1].rate;
      }
      this.bills_in.forEach((d) => {
        if(!d.is_delete) {
          // if (d.amount_unit === "USDT") {
          //   this.statisticsObject.totalIncomeUSDT = calculateNumber(
          //     this.statisticsObject.totalIncomeUSDT,
          //     d.amount_num,
          //     "add"
          //   );
          // } else {
          //   this.statisticsObject.totalIncomeRMB = calculateNumber(
          //     this.statisticsObject.totalIncomeRMB,
          //     d.amount_num,
          //     "add"
          //   );
          // }
          this.statisticsObject.totalIncomeRMB = calculateNumber(
            this.statisticsObject.totalIncomeRMB,
            d.amount_num,
            "add"
          );
          // if (d.after_rate_amount_unit === "USDT") {
          //   this.statisticsObject.PlanTotalUSDT = calculateNumber(
          //     this.statisticsObject.PlanTotalUSDT,
          //     d.after_rate_amount_num,
          //     "add"
          //   );
          // } else {
          //   this.statisticsObject.PlanTotalRMB = calculateNumber(
          //     this.statisticsObject.PlanTotalRMB,
          //     d.after_rate_amount_num,
          //     "add"
          //   );
          // }
          this.statisticsObject.PlanTotalRMB = calculateNumber(
            this.statisticsObject.PlanTotalRMB,
            d.after_rate_amount_num,
            "add"
          );
        }
        
      });
      this.bills_out.forEach((d) => {
        if(!d.is_delete) {
          // if (d.after_rate_amount_unit === "USDT") {
          //   this.statisticsObject.issuedUSDT = calculateNumber(
          //     this.statisticsObject.issuedUSDT,
          //     d.after_rate_amount_num,
          //     "add"
          //   );
          // } else {
          //   this.statisticsObject.issuedRMB = calculateNumber(
          //     this.statisticsObject.issuedRMB,
          //     d.after_rate_amount_num,
          //     "add"
          //   );
          // }
          this.statisticsObject.issuedUSDT = calculateNumber(
            this.statisticsObject.issuedUSDT,
            d.after_rate_amount_num,
            "add"
          );
        }
       
      });

      this.statisticsObject.unissuedRMB = calculateNumber(
        this.statisticsObject.PlanTotalRMB,
        this.statisticsObject.issuedUSDT,
        "subtract"
      );
      // this.statisticsObject.unissuedUSDT = calculateNumber(
      //   this.statisticsObject.PlanTotalUSDT,
      //   this.statisticsObject.issuedUSDT,
      //   "subtract"
      // );
    },
    formatBills(res, search_bool) {
      let mark_in = res.mark_in || {};
      let mark_out = res.mark_out || {};
      let operator_in = res.operator_in || {};
      let operator_out = res.operator_out || {};
      this.markList = [];
      this.operatorList = [];
      if (mark_in.data && mark_out.data) {
        // let mark_in_list = Object.keys(mark_in.data).length >= Object.keys(mark_out.data).length ? mark_in.data : mark_out.data;
        for (let key in mark_in.data) {
          this.markList.push({
            name: key,
            sum_amount: mark_in.data[key] && mark_in.data[key].sum_amount || 0,
            after_rate_amount: mark_in.data[key] && mark_in.data[key].after_rate_amount || 0,
            before_rate_amount: mark_out.data[key] && mark_out.data[key].sum_amount || 0
            // (mark_in.data[key] && mark_in.data[key].sum_amount || 0) - (mark_out.data[key] && mark_out.data[key].sum_amount || 0),
          });
        }
        for (let key in mark_out.data) {
          let mark = this.markList.find(d => d.name === key);
          if(!mark) {
            this.markList.push({
              name: key,
              sum_amount: mark_in.data[key] && mark_in.data[key].sum_amount || 0,
              after_rate_amount: mark_in.data[key] && mark_in.data[key].after_rate_amount || 0,
              before_rate_amount: mark_out.data[key] && mark_out.data[key].sum_amount || 0
              // (mark_in.data[key] && mark_in.data[key].sum_amount || 0) - (mark_out.data[key] && mark_out.data[key].sum_amount || 0),
            });
          }
          
        }
      }
      if (operator_in.data && operator_out.data) {
        // let operator_in_list = Object.keys(operator_in.data).length >= Object.keys(operator_out.data).length ? operator_in.data : operator_out.data
        for (let key in operator_in.data) {
          this.operatorList.push({
            name: key,
            sum_amount:  operator_in.data[key] && operator_in.data[key].sum_amount || 0,
            before_rate_amount: operator_in.data[key] && operator_in.data[key].after_rate_amount || 0,
            after_rate_amount: operator_out.data[key] && operator_out.data[key].sum_amount || 0,
            // (operator_in.data[key] && operator_in.data[key].sum_amount || 0) -
            //   (operator_out.data[key] && operator_out.data[key].sum_amount || 0),
          });
        }
        for (let key in operator_out.data) {
          let operator = this.operatorList.find(d => d.name === key);
          if(!operator) {
            this.operatorList.push({
              name: key,
              sum_amount:  operator_in.data[key] && operator_in.data[key].sum_amount || 0,
              before_rate_amount: operator_in.data[key] && operator_in.data[key].after_rate_amount || 0,
              after_rate_amount: operator_out.data[key] && operator_out.data[key].sum_amount || 0,
              // (operator_in.data[key] && operator_in.data[key].sum_amount || 0) -
              //   (operator_out.data[key] && operator_out.data[key].sum_amount || 0),
            });
          }
        }
      }
      if (this.keyword && search_bool) {
        this.markList = this.markList.filter((d) => {
          if (this.popoverText === "按标记人") {
            return d.name.indexOf(this.keyword) > -1;
          } else if (this.popoverText === "按操作人") {
            return false;
          }
        });
        this.operatorList = this.operatorList.filter((d) => {
          if (this.popoverText === "按标记人") {
            return false;
          } else if (this.popoverText === "按操作人") {
            return d.name.indexOf(this.keyword) > -1;
          }
        });
      }
      
    },
    searchHandle() {
      if (this.keyword && this.popoverText) {
        this.dataType = 2;
      } else {
        this.dataType = 1;
      }
      if(this.day === 0) {
        this.getListshandle(true);
      } else {
        this.getListshandle(true);
      }
    },
    openTime(type) {
      return;
      this.type = type;
      if (type === "start") {
        this.title = "请选择开始时间";
        if (this.endTime) {
          this.maxDate = new Date(this.endTime);
        } else {
          this.maxDate = new Date(2025, 11, 31);
        }
        if (this.startTime) {
          this.currentDate = new Date(this.startTime);
        }
        this.minDate = new Date(2020, 0, 1);
      } else if (type === "end") {
        this.title = "请选择结束时间";
        if (this.startTime) {
          this.minDate = new Date(this.startTime);
        } else {
          this.minDate = new Date(2020, 0, 1);
        }
        if (this.endTime) {
          this.currentDate = new Date(this.endTime);
        }
        this.maxDate = new Date(2025, 11, 31);
      }
      this.show = true;
    },
    confirmTimeHandler(e) {
      let time = parseTime(e, "{y}-{m}-{d} {h}:{i}");
      console.log(time);if (this.type === "start") {
        this.startTime = time;
      } else {
        this.endTime = time;
      }
      this.show = false;
    },
    onSelect(e) {
      this.popoverText = e.text;
    },
  },
};

export {
	billMixin
};